h1 {
  font-size: 1em;
  color: rgb(127, 208, 255);
  text-align: center;
  margin-bottom: 40px;
}

h2 {
  font-size: 1.2em;
  margin: 30px 0 20px 0;
}

table {
  border: 1px solid black;
  border-collapse: collapse;
}

caption {
  text-align: left;
  margin: 0 0 10px 0;
}

th {
  background-color: gray;
  color: #fff;
  padding: 10px;
}

td {
  padding: 10px;
}

.center {
  text-align: center;
}